<template>
  <div>
    <div v-if="loader" class="loader center">
      <div class="sk-folding-cube">
        <div class="sk-cube1 sk-cube"></div>
        <div class="sk-cube2 sk-cube"></div>
        <div class="sk-cube4 sk-cube"></div>
        <div class="sk-cube3 sk-cube"></div>
      </div>
    </div>

    <transition name="page" mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import router from '../router'

window.iframe = {
  goToPage(id) {
    router.replace('/iframe/pages/' + id);
  }
};

export default {
  name: 'iframe',

  data () {
    return {
      loader: true,
      data: window.parent.data,
    }
  },

  created () { 

    var vm = this;

    for(var css in this.data.cssFiles) {
      this.loadStyle(this.data.cssFiles[css]);
    }

    for(var js in this.data.jsFiles) {
      this.loadScript(this.data.jsFiles[js]);
    }


    setTimeout( function() {
      vm.loader = false;
    }, 1000);

    window.parent.builder.getIframe();
  },

  methods: {
    loadStyle: function (pathToCss) {
      var link  = document.createElement('link');
      link.rel  = 'stylesheet';
      link.type = 'text/css';
      link.href = pathToCss;
      link.media = 'all';
      document.getElementsByTagName('head')[0].appendChild(link);
    },

    loadScript: function (pathToJs) {
      var script = document.createElement('script');
      script.src = pathToJs;
      script.type = "text/javascript";
      script.async = false;
      document.getElementsByTagName('head')[0].appendChild(script);
    },

    removeScript: function (pathToScript) {
      var head = document.getElementsByTagName("head")[0];
      var scripts = head.getElementsByTagName("script");
      for (var i = 0; i < scripts.length; i++) {
          var js = scripts[i];
          if (js.src == pathToScript) {
              head.removeChild(js);
              break;
          }
      }
    },

    removeStyles: function () {
      var head = document.getElementsByTagName("head")[0];
      var styles = head.getElementsByTagName("link");
      for (var i = 0; i < styles.length; i++) {
        var style = styles[i];
        head.removeChild(style);
      }
    },
  }
}
</script>

<style scoped lang="scss">
  
  html {
    background: #fff;
  }


  .tools {
    bottom: 0;
    width: 100%;
    height: 40px;
    background: rgba(0, 123, 255, 0.43);
    visibility: hidden;
  }

  .loader {
    position: fixed;
    background: #fff;
    z-index: 9999;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; 
    display: flex; justify-content: center;align-items: center;
    border-radius: 3px;
  }


.sk-folding-cube {
  margin: 20px auto;
  width: 40px;
  height: 40px;
  position: relative;
  -webkit-transform: rotateZ(45deg);
  transform: rotateZ(45deg);
}

.sk-folding-cube .sk-cube {
  float: left;
  width: 50%;
  height: 50%;
  position: relative;
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1); 
}
.sk-folding-cube .sk-cube:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #25d8bd;
  -webkit-animation: sk-foldCubeAngle 2.4s infinite linear both;
  animation: sk-foldCubeAngle 2.4s infinite linear both;
  -webkit-transform-origin: 100% 100%;
  -ms-transform-origin: 100% 100%;
  transform-origin: 100% 100%;
}
.sk-folding-cube .sk-cube2 {
  -webkit-transform: scale(1.1) rotateZ(90deg);
          transform: scale(1.1) rotateZ(90deg);
}
.sk-folding-cube .sk-cube3 {
  -webkit-transform: scale(1.1) rotateZ(180deg);
          transform: scale(1.1) rotateZ(180deg);
}
.sk-folding-cube .sk-cube4 {
  -webkit-transform: scale(1.1) rotateZ(270deg);
          transform: scale(1.1) rotateZ(270deg);
}
.sk-folding-cube .sk-cube2:before {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}
.sk-folding-cube .sk-cube3:before {
  -webkit-animation-delay: 0.6s;
          animation-delay: 0.6s; 
}
.sk-folding-cube .sk-cube4:before {
  -webkit-animation-delay: 0.9s;
          animation-delay: 0.9s;
}
@-webkit-keyframes sk-foldCubeAngle {
  0%, 10% {
    -webkit-transform: perspective(140px) rotateX(-180deg);
            transform: perspective(140px) rotateX(-180deg);
    opacity: 0; 
  } 25%, 75% {
    -webkit-transform: perspective(140px) rotateX(0deg);
            transform: perspective(140px) rotateX(0deg);
    opacity: 1; 
  } 90%, 100% {
    -webkit-transform: perspective(140px) rotateY(180deg);
            transform: perspective(140px) rotateY(180deg);
    opacity: 0; 
  } 
}

@keyframes sk-foldCubeAngle {
  0%, 10% {
    -webkit-transform: perspective(140px) rotateX(-180deg);
            transform: perspective(140px) rotateX(-180deg);
    opacity: 0; 
  } 25%, 75% {
    -webkit-transform: perspective(140px) rotateX(0deg);
            transform: perspective(140px) rotateX(0deg);
    opacity: 1; 
  } 90%, 100% {
    -webkit-transform: perspective(140px) rotateY(180deg);
            transform: perspective(140px) rotateY(180deg);
    opacity: 0; 
  }
}

  .page-enter-active, .page-leave-active {
    transition: opacity 0.2s ease-out, transform 0.2s ease-out;
  }

  .page-enter, .page-leave-to {
    opacity: 0;
    transform: translateX(-10%);
  }
</style>



